import request from "../utils/request"

//   
export function getPrescriptionList(data) {
    return request({
        url: "/pres/list",
        method: "get",
        data
    })
}

//   
export function getUiyakpoomList(data) {
    return request({
        url: "/pres/uiyakpoom",
        method: "post",
        data
    })
}

//  
export function getPrescriptionRegister(data) {
    return request({
        url: "/pres/register",
        method: "post",
        data
    })
}

//  PDF  
export function createPrescription(data) {
    return request({
        url: "/pres/pdf",
        method: "post",
        data
    })
}